import React from 'react';
import { GetServerSideProps, NextPage } from 'next';
import { Newsletter } from '../../types';
import { NEWSLETTER_ITEMS } from '@/app/mocks/newsletters';
import { USER_WITH_ONE_SUBSCRIPTION } from '../../mocks/user';
import NewsletterListItem from "../NewsletterListItem/NewsletterListItem";
import styles from "./newsletterList.module.scss";

type NewsletterListProps = {
  newsletters: Newsletter[];
  sites: string[];
};

const NewsletterList: NextPage<NewsletterListProps> = ({ newsletters, sites }) => {
  return (
    <div className={styles.newsletterList}>
      {sites.map((site) => (
        <React.Fragment key={site}>
          <h2>{site}</h2>
          <div className={styles.newsletterListGrid}>
            {newsletters
              .filter((newsletter: Newsletter) => newsletter.site === site)
              .map((item) => (
                <NewsletterListItem
                  item={item}
                  key={item.id}
                  userCanSubscribe={item.userCanSubscribe}
                />
              ))}
          </div>
        </React.Fragment>
      ))}
    </div>
  );
};

// Cette fonction est exécutée côté serveur pour récupérer les données avant le rendu
export const getServerSideProps: GetServerSideProps = async () => {
  // Récupérer les newsletters depuis l'API
  const res = await fetch('/api/newsletters');
  const newsletters: Newsletter[] = await res.json();

  // Récupérer la liste des sites
  const sites = await getSite();
  
  return {
    props: {
      newsletters,
      sites,
    },
  };
};

// Cette fonction récupère la liste des sites
async function getSite(): Promise<string[]> {
  let allSites: Array<string> = [];
  NEWSLETTER_ITEMS.forEach((item: Newsletter) => {
    if (!allSites.includes(item.site)) {
      allSites.push(item.site);
    }
  });
  return allSites;
}

export default NewsletterList;
